nav{
    width: 100%;
    /* background: #333; */
    color:#fff;
    padding: 4px 0;
    position: fixed;
    top:0;
    left:0;
    display:flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
    padding: 1vh 0 1vh 0.5vw;
}
.logo{
    width: 100px;
    height: 80px;  
    background-color:transparent;  
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;
}
.dark-nav{
    background: #6e75b6;
    transition: 0.5s;
}